// src/components/Home.js
import React, {useRef, useEffect} from "react";
import ReactHtmlParser from 'react-html-parser'; 
import { processComponentProperties } from "../commons/CommonUtils.js";
import wrapper from "../commons/WebComponentWrapper";

/*
    <srs-richtext html_string=`<div/>` event_handlers=[{querySelector: ".element", sourceEventName: "INPUT#clicked", targetEventName: "RICHTEXT#clicked"}, {...}]/>
*/
const RichText = function(props, ref) {
    useEffect(() => {
        let uProps = processComponentProperties(props);
        if(uProps.event_handlers) {
            uProps.event_handlers.map((eventHandler) => {
                let querySelector = eventHandler.querySelector;
                let div = ref.current;
                let matchedElements = div.querySelector(querySelector);
                matchedElements.addEventListener(eventHandler.sourceEventName || "click", (e) => {
                    props.dispatchEvent(eventHandler.targetEventName, {payload: e.target});
                });
            });
        }
    }, [props.html_string, props.event_handlers])
    return (<div> 
        { ReactHtmlParser (props.html_string) }
    </div>);
}

const styles = `
    
`

const props = [{
    name: "html_string",
    defaultValue: "",
    type: "string"
}, {
    name: "event_handlers",
    defaultValue: [],
    type: "object"
}]

const WrappedComponent = wrapper(React.forwardRef(RichText), props);
export {WrappedComponent, styles, props};