import { jssPreset } from '@mui/styles';
import { create } from 'jss';

export const processComponentProperties = function (props) {        
    let result = {};
    let propKeys = Object.keys(props);
    propKeys.map((propKey) => {
        if(typeof props[propKey] == "string") {
            try {
                result[propKey] = JSON.parse(props[propKey]);
            }catch(err) {
                result[propKey] = props[propKey];
            } 
        }
    });   
    return result;
}

export const getUniqueElementId = () => {
    return 'xxxxyyyyxxxxyyyyxyxyyxyxxyxyyxyx'.replace(/[xy]/g, function(c) {
        var r = Math.random()*16|0, v = c == 'x' ? r : (r&0x3|0x8);
        return v.toString(16);
    });
}